.button_up{
    position: fixed;
    right: 30px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: rgba(127, 255, 246, 0.659);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    cursor: pointer;
    img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }
}


